<template>
<div class="auto">
  <div class="yan"></div>
  <div class="yan1"></div>
  <el-transfer
    filterable
    :filter-method="filterMethod"
    filter-placeholder="请输入城市拼音"
    v-model="value"
    :titles="['未绑定', '已绑定']"
    :button-texts="['解绑', '绑定']"
    @change="change"
    @left-check-change="left"
    @right-check-change="right"
    :data="data"
  ></el-transfer>
  </div>
</template>

<script>
export default {
  data() {
    const generateData = _ => {
      const data = [];
      const cities = ["手环一", "手环二", "手环三", "手环四", "手环五", "手环六", "手环七"];
      const pinyin = [
        "shouhuanyi",
        "shouhuaner",
        "shouhuansan",
        "shouhuansi",
        "shouhuanwu",
        "shouhuanliu",
        "shanhuanqi"
      ];
      cities.forEach((city, index) => {
        data.push({
          label: city,
          key: index,
          pinyin: pinyin[index],
          disabled: index !== 1
        });
      });
      return data;
    };
    return {
      data: generateData(),
      value: [1],
      filterMethod(query, item) {
        return item.pinyin.indexOf(query) > -1;
      }
    };
  },
  methods: {
    change(value, b, c) {
      if (value.length == 0) {
        for (var k = 0; k < this.data.length; k++) {
          this.data[k].disabled = false;
        }
      } else if (value.length == 1) {
        for (var k = 0; k < this.data.length; k++) {
          this.data[k].disabled = true;
          this.data[value].disabled = false;
          console.log(value);
        }
      }

      console.log(value);
    },
    left(value,b){
        if(b.length==0){
            console.log("不可以全选")
        }
        else{
        if(value.length!=0){
        for (var k = 0; k < this.data.length; k++) {
          this.data[k].disabled = true;
          this.data[value].disabled = false;
        }}
        else{
           for (var k = 0; k < this.data.length; k++) {
          this.data[k].disabled = false;
         
        }
        }
        }
        console.log(value)
        console.log(b)
    }
  },
  computed: {}
};
</script>
<style>
.auto{
        margin:  auto;
   position: relative;
    width: 777px;
}
.yan{
     position: absolute;
    width: 33px;
    height: 32px;
    background-color: #f5f7fa;
    z-index: 99999;
    top: 2px;
    left: 2px;
}
.yan1{
     position: absolute;
    width: 33px;
    height: 32px;
    background-color: #f5f7fa;
    z-index: 99999;
    top: 2px;
    left: 326px;
}
</style>



