<template>
  <div class="zhuinfo">
    <table>
      <tr style="height:80px">
        <td style="width:100px">住院号</td>
        <td style="width:392px">
          <el-input style="width:235px" v-model="input" placeholder="请输入内容"></el-input>
        </td>
        <td style="width:100px" >所在房间ID</td>
        <td style="width:392px">
          <el-input style="width:235px" v-model="input" placeholder="请输入内容"></el-input>
        </td>
        <td style="width:100px">所属医生</td>
        <td >
          <el-input style="width:235px" v-model="input" placeholder="请输入内容"></el-input>
        </td>
      </tr>
      <tr style="height:80px">
        <td style="width:100px">所属病区</td>
        <td>
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px">电子围栏</td>
        <td>
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px">是否在院</td>
        <td>
         <el-select v-model="zaiyuanstatevalue" placeholder="请选择">
            <el-option
              v-for="item in zaiyuanstate"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
      </tr>
      <tr style="height:80px">
        <td>护理等级</td>
        <td>
         <el-select v-model="gradevalue" placeholder="请选择">
            <el-option
              v-for="item in grade"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px">健康状况</td>
        <td>
          <el-select v-model="Healthyvalue" placeholder="请选择">
            <el-option
              v-for="item in Healthy"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select> 
        </td>
        <td style="width:100px">身体状况陈述</td>
        <td>
         <el-input style="width:235px" v-model="input" placeholder="请输入内容"></el-input>
        </td>
      </tr>
      <tr style="height:80px">
        <td style="width:100px">照护重点</td>
        <td>
          <el-input style="width:235px" v-model="input" placeholder="请输入内容"></el-input>
        </td>
        <td style="width:100px">饮食禁忌</td>
        <td>
          <el-input style="width:235px" v-model="input" placeholder="请输入内容"></el-input>
        </td>
        <td>
         血型
        </td>
        <td>
           <el-select v-model="bloodvalue" placeholder="未知" >
            <el-option
              v-for="item in blood"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
      </tr>
      <tr style="height:80px">
        <td style="width:100px">药物反应</td>
        <td>
          <el-input style="width:235px" v-model="input" placeholder="请输入内容"></el-input>
        </td>
        <td style="width:100px">备注</td>
        <td>
          <el-input style="width:235px" v-model="input" placeholder="请输入内容"></el-input>
        </td>
        <td style="width:100px"></td>
        <td>
          
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
export default {
    data() {
      return {
grade:[
        {
          value: "全自理",
          label: "全自理"
        },
        {
          value: "半失能",
          label: "半失能"
        },
        {
          value: "全失能",
          label: "全失能"
        }
      ],
      gradevalue: "",
      Healthy:[
        {
          value: "优秀",
          label: "优秀"
        },
        {
          value: "良好",
          label: "良好"
        },
        {
          value: "较差",
          label: "较差"
        }
      ],
      Healthyvalue: "",
      zaiyuanstate:[
        {
          value: "在院",
          label: "在院"
        },
        {
          value: "离院",
          label: "离院"
        }
      ],
      zaiyuanstatevalue: "",
      blood:[
        {
          value: "A型",
          label: "A型"
        },
        {
          value: "B型",
          label: "B型"
        },
        {
          value: "O型",
          label: "O型"
        },{
          value: "AB型",
          label: "AB型"
        },{
          value: "Rh阴性A型",
          label: "Rh阴性A型"
        },{
          value: "Rh阴性B型",
          label: "Rh阴性B型"
        },{
          value: "Rh阴性O型",
          label: "Rh阴性O型"
        },{
          value: "Rh阴性AB型",
          label: "Rh阴性AB型"
        },
      ],
      bloodvalue: "",
        
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
      };
    }
};
</script>
<style>
.zhuinfo table {
  border: none;
}
</style>




